import { Component, OnInit, ViewChild, ViewChildren, Input, OnDestroy } from '@angular/core';
import { PerfectScrollbarDirective } from 'ngx-perfect-scrollbar';
import { ChatService, ChatCollection, User, Chat } from '../../../chat/chat.service';
import { NgForm } from '@angular/forms';
import { Subscription } from 'rxjs';
import { SharedAnimations } from '../../../../shared/animations/shared-animations';
import { Company, CompanyService } from '../company.service';
import { Router, ActivatedRoute } from '@angular/router';
import { AuditService, Audit } from '../../../../shared/services/audit.service';
import { AttachmentService } from '../../../../shared/services/attachment.service';
import { saveAs } from 'file-saver';
import { AuditResponse, AuditEvent } from '../../../../shared/models';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-company-content',
  templateUrl: './company-content.component.html',
  styleUrls: ['./company-content.component.scss'],
  animations: [SharedAnimations]
})
export class CompanyContentComponent implements OnInit, OnDestroy {
  user: User = new User();
  activeContact: User = new User();
  activeCompany: Company = new Company();
  public chatCollection: ChatCollection;

  userUpdateSub: Subscription;
  chatUpdateSub: Subscription;
  chatSelectSub: Subscription;

  loadAuditDataSub: Subscription;
  companySelectSub: Subscription;
  companyId: number;
  showHistory = true;

  loadingData = false;
  showAuditProgress: boolean;
  

  audits: AuditEvent[];
  auditEventDetail: string[];


  // tslint:disable-next-line:no-input-rename
  @Input('matSidenav') matSidenav;
  @ViewChild(PerfectScrollbarDirective) psContainer: PerfectScrollbarDirective;

  @ViewChildren('msgInput') msgInput;
  @ViewChild('msgForm') msgForm: NgForm;

  constructor(public companyService: CompanyService,
    private auditService: AuditService,
    private router: Router,
    private modalService: NgbModal,
    private attachmentService: AttachmentService,
    private toastr: ToastrService,
    private route: ActivatedRoute) {

  }

  ngOnInit() {
    
    this.route.params.subscribe(params => this.companyId = params['id']);
    this.activeCompany = null;
    
    this.chatSelectSub = this.companyService.onCompanySelected.subscribe(res => {
      if (res) {
        this.activeCompany = res.company;
        this.showHistory = false;
        this.audits = [];
        this.loadingData = false;
      }
    });
  }

  ngOnDestroy() {
    if (this.userUpdateSub) { this.userUpdateSub.unsubscribe(); }
    if (this.chatSelectSub) { this.chatSelectSub.unsubscribe(); }
    if (this.chatUpdateSub) { this.chatUpdateSub.unsubscribe(); }
    if (this.companySelectSub) { this.companySelectSub.unsubscribe(); }
  }

  

  edit(company) {
    this.router.navigate(['edit', company.id], { relativeTo: this.route });
  }

  toggleShowAudit() {
    debugger;
    this.showAuditProgress = true;
    this.showHistory = !this.showHistory;
    if (this.showHistory) {
      this.loadAuditDataSub = this.auditService.getAuditTrails('company', this.activeCompany.id)
        .subscribe(res => {
          this.audits = res.events;
          this.showAuditProgress = false;
        });
    }
  }

  clone(company: Company) {
    this.router.navigate(['clone', company.id], {relativeTo: this.route})
  }

  open(content, eventId) {
    this.auditService.getAuditEventDetail(eventId).subscribe(
      response => {
        debugger;
        this.auditEventDetail = response.changeDetails;
        this.modalService.open(content, { ariaLabelledBy: 'modal-basic-title' })
      .result.then((result) => {
        console.log(result);
      }, (reason) => {
        console.log('Err!', reason);
      });
      },
      error => {
        console.log(error);
      }
    )
    
  }

  confirm(content) {
    this.modalService.open(content, { ariaLabelledBy: 'modal-basic-title', centered: true })
    .result.then((result) => {
        this.loadingData = true;
        this.companyService.delete(this.activeCompany.id)
          .subscribe(
            res => {
              if (res) {
                this.companyService.loadCompanyData(0, 6);
                this.toastr.success('Company successfully deleted !', 'Success', { timeOut:4000 });
              } else {
                this.toastr.error(res.errorMessage, 'Failed', { timeOut: 4000});
              }
              this.loadingData = false;
              
            }, 
            error => {
              this.toastr.error(error.errorMessage, 'Failed', { timeOut: 30000});
              this.loadingData = false;
            }
          )
    }, (reason) => {
      console.log("model reason : " + reason);
    });
  }


  download(id: number) {
    this.attachmentService.download(id).subscribe(res => {
      if (res) {
        saveAs('data:application/text;base64,' + res.content, res.name);
      }
    });
  }
}
