import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { CompanyService, Company } from '../company.service';
import { Subscription, Observable } from 'rxjs';
import { Router, ActivatedRoute } from '@angular/router';
import { FormGroup, FormControl, FormBuilder, FormsModule } from '@angular/forms';
import { map, switchMap, startWith, debounceTime } from 'rxjs/operators';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'app-company-left-pane',
  templateUrl: './company-left-pane.component.html',
  styleUrls: ['./company-left-pane.component.scss']
})
export class CompanyLeftPaneComponent implements OnInit {

  advancedSearchSub: Subscription;

  companies: Company[];
  filteredCompanies: Company[];
  loadDataSub: Subscription;
  selectedAction = 'All';
  loadingCollection = false;
  advancedSearchApplied = false;

  page = 1;
  pageSize = 6;
  collectionSize: number;
  
  searchControl: FormControl = new FormControl();

  filterAction: string[] = ['All', 'Active', 'Inactive'];

  advancedSearchForm: FormGroup;
  searchedCompanies: Observable<Company[]>;

  public userInput = new FormControl();

  constructor(private companyService: CompanyService,
    private router: Router,
    private route: ActivatedRoute,
    private modalService: NgbModal,
    private fb: FormBuilder) {
  }


  ngOnInit(): void {
    
    this.loadingCollection = true;
    this.setAdvancedSearchForm();

    this.loadDataSub = this.companyService.loadCompanyData(this.page, this.pageSize)
      .subscribe(res => {
        this.companies = res.content;
        this.filteredCompanies = this.companies;
        this.collectionSize = res.totalElements;
       this.loadingCollection = false;
    });

    this.searchControl.valueChanges
      .pipe(debounceTime(200))
      .subscribe(value => {
        this.filerData(value);
    });

    // Listen for company change
    this.advancedSearchSub = this.companyService.onAdvancedSearchApplied.subscribe(res => {
      if (res) {
        debugger;
        this.companies = res.companies.content;
        this.filteredCompanies = this.companies;
        this.collectionSize = res.companies.totalElements;
      }
    });

  }

  filerData(val) {
    let query = '?name=' + val;
    if (this.selectedAction.toLocaleLowerCase() != 'all') {
      query = query + '&status=' + this.selectedAction.toUpperCase();
    }
    this.companyService.performAdvancedSearch(query);
  }

  filerDataWithStatus(val) {
    let query = '';
    if (this.selectedAction.toLocaleLowerCase() != 'all') {
      query = query + '?status=' + this.selectedAction.toUpperCase();
    }
    this.companyService.performAdvancedSearch(query);
  }

  getCompanyByid(companyId) {
    this.companyService.getCompanyById(companyId)
      .subscribe(res => {

      }, err => {
        console.log(err);
      });
  }

  getPageFromService(page: number) {
    this.loadingCollection = true;
    this.companyService.loadCompanyData(page, this.pageSize)
      .subscribe(res => {
        this.companies = res.content;
        this.filteredCompanies = this.companies;
        this.collectionSize = res.totalElements;
       this.loadingCollection = false;
       this.page = page;
    });
  }

  filter(action) {
    this.selectedAction = action;
    this.filerDataWithStatus(action.toString());
  }

  add() {
    this.router.navigate(['add'], { relativeTo: this.route });
  }

  setAdvancedSearchForm() {
    this.advancedSearchForm = this.fb.group({
      name: '',
      registrationNumber: '',
      status: ''
    });
  }

  open(content) {
    this.modalService.open(content, { ariaLabelledBy: 'modal-basic-title' })
    .result.then((result) => {
      console.log(result);
    }, (reason) => {
      console.log('Err!', reason);
    });
  }

  performAdvancedSearch(event) {
    this.loadingCollection = true;
    this.modalService.dismissAll('close clicked');
    this.advancedSearchApplied = true;
  }

  clearSearchCriteria() {
    this.advancedSearchApplied = false;
    this.filerData('');
  }

   showPagination() {
     if (this.filteredCompanies) {
       return this.filteredCompanies.length > 5 ? true : false;
     }
     return false;
   }

}
